* {
  font-family: 'Idealist', sans-serif;
  box-sizing: border-box !important;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.card-filter-courses {
  top: 5rem !important;
}

.card {
  grid-row: 1 / 3;
  background-size: cover;
  background-repeat: no-repeat;
}

/*main courses */
.main-courses {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 10rem 10rem;
  grid-gap: 1rem;
}

.main-courses div:first-child {
  grid-row: 1 / 3;
}

.main-courses div {
  background-repeat: no-repeat;
  background-size: cover;
  background-image: linear-gradient(rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1));
  color: #fff;
}

.bg-custom {
  background: #f4f8fa !important;
}

.header {
  clip-path: polygon(0% 0%, 100% 0%, 100% 94%, 53% 94%, 50% 100%, 45% 94%, 0 94%);
  background-image: url('./static/images/bg_1-min.jpg');
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: 0 100%;
}

.header:last-child {
  background-image: url('./static/images/bg_2.jpg');
}

.second {
  // background-repeat: repeat-y !important;
  background-position: 20% 25% !important;
}


@media (max-width: 1400px) {
  .second {
    background-position: 25% 0 !important;
  }
}

@media (min-width: 1700px) {
  .second {
    background-position: 25% 100% !important;
  }
}

.header:nth-child(2) {
  background-image: url('./static/images/bg_2.jpg');
}

.third {
  background-position: 10% 60% !important;
  background-image: url('./static/images/bg_3.jpg');
}


.fourth {
  background-position: center !important;
  background-image: url('./static/images/bg_4.jpg') !important;
}

@media (min-width: 1700px) {
  .fourth {
    background-position: 10% 40% !important;
  }
}

.fifth {
  background-position: center !important;
  background-image: url('./static/images/bg_5.jpg') !important;
}

.bg-example {
  background-position: center;
}

.w-custom {
  width: 650px !important;
}

.li::before {
  content: '\25A0';
  color: crimson;
  margin-right: 5px;
}

/* Chrome, Safari, Edge, Opera */
input[type=number]::-webkit-outer-spin-button,
input[type=number]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

.tooltip-in::before {
  content: '';
  position: absolute;
  display: inline-block;
  height: 0;
  width: 0;
  border-top: 1rem solid transparent;
  border-right: 1rem solid transparent;
  border-left: 1rem solid transparent;
  border-bottom: 1rem solid white;
  top: -2rem;
  left: calc(50% - 1rem);
}

.lds-dual-ring {
  display: inline-block;
  width: 80px;
  height: 80px;
}
.lds-dual-ring:after {
  content: " ";
  display: block;
  width: 64px;
  height: 64px;
  margin: 8px;
  border-radius: 50%;
  border: 6px solid #ccc;
  border-color: #ccc transparent #ccc transparent;
  animation: lds-dual-ring 1.2s linear infinite;
}
@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

